.wrapper .header {
  width: 100%;
}
.wrapper .header .header__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
  padding: 20px 0;
  font-size: 20px;
  margin: 0 auto;
  border-bottom: 2px solid rgba(128, 128, 128, 0.4);
  font-weight: 500;
}
.wrapper .header .header__container .header__logo {
  color: #5ca1d4;
  font-weight: 600;
}
.wrapper .header .header__container .header__links {
  display: flex;
  flex-direction: row;
  align-self: center;
}
.wrapper .header .header__container .header__links .links__decoration {
  margin: 0 10px;
  display: block;
  height: 25px;
  width: 2px;
  background: #5ca1d4;
}
.wrapper .header .header__container .header__links .link {
  color: #4d6d9a;
}
.wrapper .header .header__container .header__links .link.active {
  color: #5ca1d4;
}
@media only screen and (max-width: 767.98px) {
  .wrapper .header .header__container .header__links .link .link-name {
    display: none;
  }
  .wrapper .header .header__container .header__date {
    display: none;
  }
}
.wrapper .main {
  flex: 1 1 auto;
}
.wrapper .main .main__container {
  display: flex;
  flex-direction: column;
  padding: 0 0 20px 0;
}
.wrapper .main .main__container .main__navigation {
  z-index: 20;
  width: 90%;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  position: relative;
}
.wrapper .main .main__container .main__navigation .react-select-container {
  width: 220px;
}
.wrapper .main .main__container .main__navigation .navigation__button-open-setting {
  outline: none;
  border: 0;
  font-size: 20px;
  background: none;
  margin-right: 10px;
}
.wrapper .main .main__container .main__navigation .navigation__settings-window {
  position: absolute;
  width: 300px;
  height: 300px;
  background: rgba(248, 248, 255, 0.9);
  bottom: 0;
  right: 0;
  transform: translate(0, 100%);
  border-radius: 10px;
  box-shadow: 0 0 6px 4px rgba(113, 105, 105, 0.3);
  padding: 20px;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out all;
}
.wrapper .main .main__container .main__navigation .navigation__settings-window.active {
  visibility: visible;
  opacity: 1;
}
.wrapper .main .main__container .main__content {
  width: 90%;
  margin: 0 auto;
}
.wrapper .main .main__container .main__content .content__full-schedule {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__week {
  max-width: 400px;
  width: 100%;
  text-align: center;
  margin-bottom: 30px;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__week .week-name {
  font-size: 25px;
  font-weight: 600;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container {
  display: grid;
  grid-template-columns: 15px minmax(220px, 1fr);
  grid-gap: 5px;
  width: 100%;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__time-column {
  display: grid;
  grid-template-rows: 15px repeat(6, 130px);
  grid-gap: 5px;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__time-column .table__lesson-time {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__time-column .table__lesson-time span {
  font-weight: 600;
  writing-mode: vertical-rl;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .swiper {
  width: 100%;
  position: relative;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .swiper-wrapper {
  /* display: grid;
   grid-template-columns: repeat(12, 1fr);
   grid-template-rows: 1fr;*/
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table {
  width: 100%;
  margin: 0 auto;
  min-height: 150px;
  font-size: 15px;
  line-height: 1.1;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__column {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr;
  grid-template-rows: 15px repeat(6, 1fr);
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__weekday {
  grid-row: 1;
  text-align: center;
  font-weight: 600;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson {
  height: 130px;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.white.empty .lesson__container {
  background: #ebebeb;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.white .lesson__container {
  background: #90cbf3;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.empty .lesson__container {
  opacity: 0.2 !important;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.filtered .lesson__container {
  opacity: 0.4;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.today .lesson__container {
  background: #5ca1d4;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.today.closest .lesson__container {
  background: #f58786;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson.closest .lesson__container {
  background: #f2d14f;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container {
  transition: 0.1s ease-in-out all;
  color: #232323;
  padding: 10px 10px 5px;
  width: 100%;
  height: 100%;
  max-height: 100%;
  background: #ebebeb;
  border-radius: 10px;
  /*display: grid;
  grid-template-columns: 1fr;*/
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container:hover {
  min-height: 100%;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__name {
  font-size: 16px;
  font-weight: 600;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__links {
  flex: 1 1 auto;
  /*display: flex;
  flex-direction: row;
  flex-wrap: wrap;*/
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__links a {
  color: black;
  opacity: 0.8;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__links a:hover {
  opacity: 1;
  text-decoration: underline;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__type {
  cursor: pointer;
  align-self: flex-end;
  opacity: 0.5;
}
.wrapper .main .main__container .main__content .content__full-schedule .full-schedule__table-container .full-schedule__table .table__lesson .lesson__container .lesson__type:hover {
  opacity: 1;
}

