.header {
  width: 100%;

  .header__container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    width: 90%;
    padding: 20px 0;
    font-size: 20px;
    margin: 0 auto;
    border-bottom: 2px solid rgba(128, 128, 128, 0.4);


    font-weight: 500;

    .header__logo {
      color: $blue2;
      font-weight: 600;
    }

    .header__links {
      display: flex;
      flex-direction: row;
      align-self: center;

      .links__decoration {
        margin: 0 10px;
        display: block;
        height: 25px;
        width: 2px;
        background: $blue2;
      }

      .link {
        color: $blue;

        &.active {
          color: $blue2;
        }
      }
    }

    .header__date {
    }
  }
}

@media only screen and(max-width: 767.98px) {
  .header {
    .header__container {
      .header__links {
        .link {
          .link-name {
            display: none;
          }
        }
      }

      .header__date {
        display: none;
      }
    }
  }
}