@import "./../variables";

.main {
  flex: 1 1 auto;

  .main__container {
    display: flex;
    flex-direction: column;
    padding: 0 0 20px 0;

    .main__navigation {
      z-index: 20;
      width: 90%;
      margin: 0 auto;
      padding: 20px 0;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: flex-end;
      position: relative;

      //Select Group
      .react-select-container {
        width: 220px;

        .react-select__control {
          .react-select__value-container {
          }

          .react-select__indicators {
          }
        }

        .react-select__menu {
          .react-select__menu-list {
            .react-select__option {
            }
          }
        }
      }

      .navigation__button-open-setting {
        outline: none;
        border: 0;
        font-size: 20px;
        background: none;
        margin-right: 10px;
      }

      .navigation__settings-window {
        position: absolute;
        width: 300px;
        height: 300px;
        background: rgba(248, 248, 255, 0.9);
        bottom: 0;
        right: 0;
        transform: translate(0, 100%);
        border-radius: 10px;
        box-shadow: 0 0 6px 4px rgba(113, 105, 105, 0.3);
        padding: 20px;
        visibility: hidden;
        opacity: 0;
        transition: 0.3s ease-in-out all;

        &.active {
          visibility: visible;
          opacity: 1;
        }
      }
    }

    .main__content {
      width: 90%;
      margin: 0 auto;

      .content__full-schedule {
        display: flex;
        flex-direction: column;
        align-items: center;
        //padding: 20px;

        .full-schedule__week {
          max-width: 400px;
          width: 100%;
          text-align: center;

          .week-name {
            font-size: 25px;
            font-weight: 600;
          }

          margin-bottom: 30px;
        }

        .full-schedule__table-container {
          //max-width: 1455px;
          display: grid;
          grid-template-columns: 15px minmax(220px, 1fr);
          grid-gap: 5px;
          width: 100%;

          .full-schedule__time-column {
            display: grid;
            grid-template-rows: 15px repeat(6, 130px);
            grid-gap: 5px;

            .table__lesson-time {
              display: flex;
              align-items: center;
              justify-content: center;

              span {
                font-weight: 600;
                writing-mode: vertical-rl;
              }
            }
          }

          //Schedule-table
          .swiper {
            //max-width: 1440px;
            width: 100%;
            position: relative;
          }

          .swiper-wrapper {
            /* display: grid;
             grid-template-columns: repeat(12, 1fr);
             grid-template-rows: 1fr;*/
          }

          .full-schedule__table {
            width: 100%;
            margin: 0 auto;
            //max-width: 1440px;
            // grid-gap: 5px;
            min-height: 150px;
            // display: grid;
            font-size: 15px;
            line-height: 1.1;
            //grid-template-columns:repeat(6, 1fr);
            //  grid-template-rows: auto repeat(6, 1fr);

            .table__column {
              display: grid;
              grid-gap: 5px;
              grid-template-columns: 1fr;
              grid-template-rows: 15px repeat(6, 1fr);
            }

            .table__weekday {
              grid-row: 1;
              text-align: center;
              font-weight: 600;
            }


            .table__lesson {
              height: 130px;

              &.white {
                &.empty {
                  .lesson__container {
                    background: $light-grey;
                  }
                }

                .lesson__container {
                  background: $sky-color;
                }
              }

              &.empty {
                .lesson__container {
                  opacity: 0.2 !important;
                }
              }

              &.filtered {
                .lesson__container {
                  opacity: 0.4;
                }
              }

              &.today {
                .lesson__container {
                  background: $blue2;
                }

                &.closest {
                  .lesson__container {
                    background: $pink;
                  }
                }
              }

              &.closest {
                .lesson__container {
                  background: $yellow;
                }
              }


              .lesson__container {
                transition: 0.1s ease-in-out all;
                color: #232323;
                padding: 10px 10px 5px;
                width: 100%;
                height: 100%;
                max-height: 100%;
                //width: 245.8px;
                //overflow: scroll;
                //background: $light-blue;
                background: $light-grey;
                border-radius: 10px;
                /*display: grid;
                grid-template-columns: 1fr;*/
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                overflow: hidden;

                &:hover {
                  min-height: 100%;
                  //max-height: 100%;
                }

                .lesson__time {
                }

                .lesson__name {
                  font-size: 16px;
                  font-weight: 600;
                }

                .lesson__links {
                  flex: 1 1 auto;
                  /*display: flex;
                  flex-direction: row;
                  flex-wrap: wrap;*/

                  a {
                    color: black;
                    opacity: 0.8;

                    &:hover {
                      opacity: 1;
                      text-decoration: underline;
                    }
                  }
                }

                .lesson__type {
                  cursor: pointer;
                  align-self: flex-end;
                  opacity: 0.5;

                  &:hover {
                    opacity: 1;
                  }
                }

              }
            }
          }
        }

      }
    }
  }
}


